import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state() {
    return {
      selectedItems: [],
    }
  },
  getters: {
    getItem: (state) => (id) => {
      return state.selectedItems.find(item => item.id === id)?.val || 0
    }
  },
  mutations: {
    setItem(state, val) {
      let index = state.selectedItems.findIndex(item => item.id === val.id)
      if (index > -1)
        state.selectedItems.splice(index, 1)
      if (val.val > 0)
        state.selectedItems.push(val)
    },

    addItem(state, val) {
      let index = state.selectedItems.findIndex(item => item.id === val.id)
      let item = {id: val.id, val: state.selectedItems[index].val + val.val}
      if (index > -1)
        state.selectedItems.splice(index, 1)
      state.selectedItems.push(item)
    }
  }
})
