<template>
  <v-app>
    <v-main>
      <v-app-bar
        :color="$store.state.selectedItems.length ? 'primary' : ''"
        class="toolbar"
      >
        <v-btn
          v-if="$store.state.selectedItems.length"
          icon
          @click="$store.state.selectedItems = []"
        >
          <v-icon>mdi-close</v-icon>
        </v-btn>

        <v-toolbar-title>
          {{ $store.state.selectedItems.length
             ? `${$store.state.selectedItems.reduce((sum, i) => sum + i.val, 0)} selected`
             : 'Test Shop' }}
        </v-toolbar-title>

        <v-spacer></v-spacer>

        <DialogItem
          v-if="$store.state.selectedItems.length"
        />
      </v-app-bar>

      <CardItems :items="items" />
      <div class="text-center">
        <v-pagination
          v-model="page"
          :length="length"
          :total-visible="5"
          class="mb-2"
          circle
        ></v-pagination>
      </div>

      <div
        class="pt-10"
        v-if="$store.state.selectedItems.length"
      ></div>
    </v-main>
  </v-app>
</template>

<script>
import CardItems from './components/Items/CardItems.vue'
import DialogItem from './components/Cart/DialogItem.vue'

import gql from 'graphql-tag'

export default {
  name: 'App',

  components: {
    CardItems,
    DialogItem,
  },

  data: () => ({
    items: [],
    page: 1,
    per_page: 20,
    length: 1,
  }),

  apollo: {
    length: {
      query: gql`query {
        productsCount
      }`,
      update(data){ return parseInt(data.productsCount / this.per_page) + 1 }
    },
    items: {
      query: gql`query ($page: Int, $perPage: Int) {
        products(page: $page, perPage: $perPage) {
          id
          title: name
          description
          price
          url: imageUrl
        }
      }`,
      update: data => data.products,
      variables() {
        return {
          page: this.page,
          perPage: this.per_page
        }
      }
    }
  },

  watch: {
    page() {
      window.scrollTo(0, 0)
    }
  },
}
</script>

