<template>
  <div class="d-flex flex-column pa-4">
    <div
      v-for="item in items"
      :key="item.id"
    >
      <CartItem
        :id="item.id"
        :title="item.title"
        :imageUrl="item.imageUrl"
        :price="item.price"
      />
    </div>
  </div>
</template>

<script>
import CartItem from './CartItem.vue'

import gql from 'graphql-tag'

export default {
  name: 'CartItems',

  components: {
    CartItem,
  },

  data: () => ({
    items: [],
    ids: []
  }),

  created() {
    this.ids = this.$store.state.selectedItems.map(item => item.id)
  },

  mounted() {
    this.ids = this.$store.state.selectedItems.map(item => item.id)
  },

  apollo: {
    items: {
      query: gql`query ($ids: [Int!]!) {
        products: productsByIds(ids: $ids) {
          id
          title: name
          price
          imageUrl
        }
      }`,
      update: data => data.products,
      variables() {
        return {
          ids: this.ids
        }
      }
    }
  }
}
</script>

