<template>
  <v-card
    class="mx-auto my-2"
    outlined
  >
    <v-list-item class="px-2">
      <v-list-item-avatar
        tile
        size="100"
        v-if="imageUrl"
      >
        <v-img
          :src="imageUrl"
          height="100"
          width="100"
        ></v-img>
      </v-list-item-avatar>
      <div class="pl-2" v-else></div>

      <v-list-item-content
        class="px-0 d-flex align-self-start"
        style="min-height: 100px"
      >
        <v-list-item-title class="title mb-1">
          <div class="d-flex align-self-center">
            {{ title }}
          </div>

          <v-spacer />

          <div class="tg-fg-link-color">
            {{ $store.getters.getItem(id) }}x
          </div>
        </v-list-item-title>

        <div class="d-flex align-self-end pr-2">
          <div class="d-flex align-self-center">
            {{ (parseFloat(price) * $store.getters.getItem(id)).toFixed(2) }} $
          </div>

          <v-spacer />

          <div>
            <v-btn
              icon
              color="secondary"
              @click="remove()"
            >
              <v-icon>mdi-delete</v-icon>
            </v-btn>
          </div>
        </div>
      </v-list-item-content>
    </v-list-item>
  </v-card>
</template>

<script>
export default {
  name: 'CartItem',

  props: {
    id: {
      type: Number,
      required: true
    },
    title: {
      type: String,
      required: true
    },
    imageUrl: {
      type: String,
    },
    price: {
      type: String,
    },
  },

  methods: {
    remove() {
      this.$store.commit('setItem', {id: this.id, val: 0})
    }
  },
}
</script>

