import Vue from 'vue'
import Vuetify from 'vuetify/lib'

Vue.use(Vuetify)

export default new Vuetify({
  theme: {
    dark: window.Telegram.WebApp.colorScheme === 'dark',
    themes: {
      light: {
        primary: window.Telegram.WebApp.themeParams.button_color || '#039BE5',
        secondary: window.Telegram.WebApp.themeParams.hint_color || '#F39BE5',
      },
      dark: {
        primary: window.Telegram.WebApp.themeParams.button_color || '#039BE5',
        secondary: window.Telegram.WebApp.themeParams.hint_color || '#F39BE5',
      }
    }
  },
})
